<template>
  <div class="type-ahead" style="position: relative" :class="{open:showDropdown}">
    <input class="form-control" autocomplete="off"
      ref="tinput"
      v-model="val"
      :placeholder="placeholder"
      :type.once="type"
      :id.once="id"
      :name.once="name"
      @blur="out"
      @keydown.down.prevent="down"
      @keydown.enter="hit"
      @keydown.esc="reset"
      @keydown.up.prevent="up"
    />
    <ul class="dropdown-menu" ref="dropdown">
      <li v-for="(item, i) in items" :class="{active: isActive(i)}">
        <a @mousedown.prevent="hit" @mousemove="setActive(i)">
          <component :is="templateComp" :item="item"></component>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>

const DELAY = 300
const LIMIT = 8

export default {
  props: {
    async: {type: String},
    data: {type: Array},
    delay: {type: Number, default: DELAY},
    asyncKey: {type: String, default: null},
    id: {type: String},
    limit: {type: Number, default: LIMIT},
    matchCase: {type: Boolean, default: false},
    matchStart: {type: Boolean, default: false},
    allowNew : {type: Boolean, default: false},
    name: {type: String},
    onHit: {
      type: Function,
      default (item) { return item }
    },
    placeholder: {type: String},
    template: {type: String},
    type: {type: String, default: 'text'},
    value: {type: String, default: ''}
  },
  data () {
    return {
      asign: '',
      showDropdown: false,
      noResults: true,
      current: 0,
      items: [],
      val: this.value
    }
  },
  computed: {
    templateComp () {
      return {
        template: typeof this.template === 'string' ? '<span>' + this.template + '</span>' : '<strong v-html="item"></strong>',
        props: { item: {default: null} }
      }
    }
  },
  watch: {
    val (val, old) {
      this.$emit('input', val)
      if (val !== old && val !== this.asign) this.__update()
    },
    value (val) {
      if (this.val !== val) { this.val = val }
    }
  },
  methods: {
    setItems (data) {
      if (this.async) {
        this.items = this.asyncKey ? data[this.asyncKey] : data
        this.items = this.items.slice(0, this.limit)
      } else {
        this.items = (data || []).filter(value => {
          if (typeof value === 'object') { return true }
          value = this.matchCase ? value : value.toLowerCase()
          var query = this.matchCase ? this.val : this.val.toLowerCase()
          return this.matchStart ? value.indexOf(query) === 0 : value.indexOf(query) !== -1
        }).slice(0, this.limit)
      }
      this.showDropdown = this.items.length > 0
    },
    setValue (value) {
      this.asign = value
      this.val = value
      this.items = []
      this.loading = false
      this.showDropdown = false
    },
    reset () { 
      this.setValue(null)
      this.$emit('reset')
    },
    setActive (index) { this.current = index },
    isActive (index) { return this.current === index },
    hit (e) {
      e.preventDefault()
      if (this.allowNew && !this.showDropdown && this.val) {
        this.setValue(this.onHit(this.val,this))
      } else {
        this.setValue(this.onHit(this.items[this.current], this))
      }
      this.$nextTick(function() {
        this.$emit('blur')
      });
    },
    up () {
      if (this.current > 0) { this.current-- }
      else { this.current = this.items.length - 1 }
    },
    down () {
      if (this.current < this.items.length - 1) { this.current++ }
      else { this.current = 0 }
    },
    focus() {
      this.$refs.tinput.focus();
    },
    out() {
      this.showDropdown = false;
      this.$emit('blur');
    }
  },
  created () {
    this.__update = _.throttle(function () {
      if (!this.val) {
        this.reset()
        return
      }
      this.asign = ''
      if (this.async) {
        var vm = this;
        axios.get(this.async + this.val)
          .then(function (response) {
              vm.setItems(response.data);
            })
          .catch(function (error) {
            console.log(error);
          });
      } else if (this.data) {
        this.setItems(this.data)
      }
    }, this.delay, { leading : false })
  }
}
</script>

<style>
  .type-ahead .dropdown-menu {
    position:absolute;
    top:100%;
    left:0px;
    min-width:160px;
    z-index:1000;
    padding:5px 0;
    margin:2px 0 0;
    list-style:none;
    text-align:left;
    border:1px solid #CCC;
    display:none;
  }
  .type-ahead .dropdown-menu > li {
    display:block;
  }
  .type-ahead .dropdown-menu > li.active {
    background-color:rgba(218,176,199,.6);
  }
  .type-ahead .dropdown-menu > li > a {
    cursor: pointer;
  }
  .type-ahead.open .dropdown-menu {
    display:block;
  }
</style>



